.main{
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    position: fixed;
    width: 100%;
    top: 0;
    background-color: #ffffffef;
    z-index: 9;
  transition: 1s;

}
.Img-div{
    display: flex;
    align-items: center;
}
/* .Img-div>div{
    margin-left: -10px;
    margin-top: -5px;
} */
.Img-div>div>h3,.Img-div>div>p{
    font-family: Raleway;
}
.vis >div a,.smallscreen >div a{
    font-size: 20px;
    font-weight: 500;
    text-decoration: none;
    color: black;
    
    
    
}
.vis >div,.smallscreen>div{
    padding: 10px;
    
}
.Img-div>div>h3{
    font-size: 24px;
    margin-left: 10px;
}
.Img-div>div>p{
    margin-top: -23px;
    color: #bbaa58ed;
    font-size: 14px;
    font-weight: bold;
}
.Img-div > img{
    width: 50px;
}
.Container{
    margin: auto;
    width: 90%;
    display: flex;
    justify-content: space-between;
}
.Button-div{
    display: flex;
    gap: 30px;
    align-items: center;
    animation: fade_in_show 1s
}
.Button-div>a{
    font-size: 20px;
    font-weight: 500;
    text-decoration: none;
    color: black;
    transition: 3s;
}
.Button-div>a:hover{
    cursor: pointer;
    color: #d2c776;
    /* text-decoration: underline; */
    transition: .8s;
}
.Button-div>a:active{
    color: #d2c776;
    text-decoration: underline;
}
.h2{
    display: none;
    align-items: center;
   
   

    /* font-size: 23px; */
}


.h21{
    display: none;
    align-items: center;
}
span{
    color: #d2c873;
    
}
.screen{
    position: fixed;
  float: right;
  top: 60px;
  padding-top: 20px;
  right: 0;
display: none;
  width: 200px;
  height: 100vh;
  background-color: #ffffff9b;
    -webkit-backdrop-filter: blur(8px);
    backdrop-filter: blur(8px);
}
.smallscreen{
 position: fixed;
  float: right;
  top: 60px;
  padding-top: 20px;
  right: 0;

  width: 200px;
  height: 100vh;
  background-color: #ffffff9b;
    animation: fade_out_hide 1s;

    -webkit-backdrop-filter: blur(8px);
    backdrop-filter: blur(8px);
    
}
body{
    padding: 0;
    margin: 0;
    background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRHpKJi0HXMugsPavxPAEiUXTRf5EpHAMgdMQ&usqp=CAU");
    background-repeat: no-repeat;
    background-size: cover;
}
button{
    border: none;
    background: none;
    color: black;
    font-size: 22px;
}
.blur{
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    position: fixed;
    width: 100%;
    top: 0;
    background-color: #ffffff9b;
    z-index: 9;
    transition: 1s;
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
}
#hid{
    display: none;
}

.vis{
  position: fixed;
  float: right;
  top: 60px;
  padding-top: 20px;
  right: 0;
  width: 200px;
  height: 100vh;
  background-color: #ffffff9b;
  display: block;
  animation: fade_in_show 1s;
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
}
@keyframes fade_in_show {
    from {
         opacity: 0;
         transform: scaleX(0)
    }

   to {
         opacity: 1;
         transform: scaleX(1)
    }
}
@keyframes fade_out_hide {
    from {
        opacity: 1;
        transform: scaleX(1);
        display: none;
   }

  to {
        opacity: 0;
        transform: scaleX(0);
        display: none;
   }
}
@media only screen and (max-width: 699px) {
    .h2 {
      display: flex;
      animation: show 1s;
    }
    .Img-div > img{
        width: 40px;
    }
    .main{
        padding: -10px 0px;
    }
    .Container{
        height: 60px;
    }
    .Img-div>div>h3{
        font-size: 20px;
    }
    @keyframes show {
        from {
             opacity: 0;
             transform: scaleX(0)
        }
    
       to {
             opacity: 1;
             transform: scaleX(1)
        }
    }
    
    .Button-div{
        display: none;
    }
    .h21{
        display: flex;
        animation: fade_in_show 1s
        
    }
    /* .smallscreen{
        display: block;
    } */
  }
  @media only screen and (max-width: 319px){
    .Img-div>div{
        display: none;
    }
    .Container{
        height: 60px;
    }
  }

  @media only screen and (min-width: 700px){
  .vis{
    display: none;
  }
  }